@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&family=Outfit:wght@300;400;500;600;700;800;900&family=Space+Grotesk:wght@300;400;500;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

/* Add gradient-radial utility */
@layer utilities {
  .bg-gradient-radial {
    background-image: radial-gradient(circle, var(--tw-gradient-stops));
  }
}

:root {
  /* Only dark theme variables - light mode removed permanently */
  --primary-color: #3b82f6;
  --secondary-color: #60a5fa;
  --accent-color: #0ea5e9;
  --text-primary: #f8fafc;
  --text-secondary: #cbd5e1;
  --bg-primary: #030712;
  --bg-secondary: #0f172a;
  --accent-primary: #3b82f6;
  --accent-secondary: #0ea5e9;
  --border-color: rgba(255, 255, 255, 0.1);
  --card-bg: rgba(17, 25, 40, 0.8);
  
  /* Theme transition */
  --transition-speed: 0.3s;

  /* Mouse-based Interactive Background Effects */
  --mouse-x: 50%;
  --mouse-y: 50%;
}

/* Dark mode is now the only mode - light mode permanently removed */

html {
  scroll-padding-top: 80px; /* For fixed header navigation */
  transition: 
    color var(--transition-speed) ease,
    background-color var(--transition-speed) ease;
}

body {
  font-family: 'Outfit', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  overflow-x: hidden;
  position: relative;
  background: 
    /* Enhanced multi-layer animated gradient background */
    linear-gradient(135deg, #020617 0%, #0f172a 25%, #1e293b 50%, #334155 75%, #0f172a 100%),
    radial-gradient(ellipse at top left, rgba(59, 130, 246, 0.1) 0%, transparent 50%),
    radial-gradient(ellipse at bottom right, rgba(147, 197, 253, 0.08) 0%, transparent 60%);
  background-size: 600% 600%, 100% 100%, 100% 100%;
  background-attachment: fixed;
  animation: enhancedGradientShift 20s ease infinite;
}

@keyframes enhancedGradientShift {
  0% {
    background-position: 0% 50%, 0% 0%, 100% 100%;
  }
  25% {
    background-position: 50% 0%, 25% 25%, 75% 75%;
  }
  50% {
    background-position: 100% 50%, 50% 50%, 50% 50%;
  }
  75% {
    background-position: 50% 100%, 75% 75%, 25% 25%;
  }
  100% {
    background-position: 0% 50%, 0% 0%, 100% 100%;
  }
}

/* Enhanced background overlay with sophisticated movement */
body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    /* Multiple sophisticated layered gradients */
    radial-gradient(ellipse 1200px 800px at 15% 85%, rgba(59, 130, 246, 0.06) 0%, transparent 60%),
    radial-gradient(ellipse 800px 1400px at 85% 15%, rgba(147, 197, 253, 0.05) 0%, transparent 70%),
    radial-gradient(ellipse 1000px 600px at 50% 20%, rgba(125, 211, 252, 0.04) 0%, transparent 80%),
    radial-gradient(ellipse 600px 1000px at 20% 50%, rgba(56, 189, 248, 0.03) 0%, transparent 75%),
    conic-gradient(from 45deg at 30% 70%, transparent 0deg, rgba(59, 130, 246, 0.02) 120deg, transparent 240deg),
    conic-gradient(from 225deg at 70% 30%, transparent 0deg, rgba(147, 197, 253, 0.03) 90deg, transparent 180deg);
  background-size: 200% 200%, 180% 180%, 160% 160%, 140% 140%, 300% 300%, 250% 250%;
  animation: sophisticatedFloatingGradients 30s ease-in-out infinite;
  pointer-events: none;
  z-index: -2;
}

@keyframes sophisticatedFloatingGradients {
  0%, 100% {
    background-position: 
      0% 0%, 100% 100%, 50% 50%, 
      25% 75%, 0% 50%, 100% 50%;
    transform: rotate(0deg) scale(1);
  }
  20% {
    background-position: 
      100% 25%, 0% 75%, 80% 20%, 
      75% 25%, 50% 0%, 50% 100%;
    transform: rotate(2deg) scale(1.02);
  }
  40% {
    background-position: 
      75% 100%, 25% 0%, 20% 80%, 
      0% 50%, 100% 50%, 0% 50%;
    transform: rotate(-1deg) scale(0.98);
  }
  60% {
    background-position: 
      50% 50%, 50% 50%, 100% 0%, 
      50% 100%, 75% 25%, 25% 75%;
    transform: rotate(3deg) scale(1.03);
  }
  80% {
    background-position: 
      25% 75%, 75% 25%, 0% 100%, 
      100% 0%, 25% 75%, 75% 25%;
    transform: rotate(-2deg) scale(0.99);
  }
}

/* Enhanced grid pattern with sophisticated multi-layer animation */
body::after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    /* Multiple grid layers for depth */
    linear-gradient(rgba(59, 130, 246, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(59, 130, 246, 0.03) 1px, transparent 1px),
    linear-gradient(rgba(147, 197, 253, 0.02) 1px, transparent 1px),
    linear-gradient(90deg, rgba(147, 197, 253, 0.02) 1px, transparent 1px),
    linear-gradient(rgba(125, 211, 252, 0.015) 1px, transparent 1px),
    linear-gradient(90deg, rgba(125, 211, 252, 0.015) 1px, transparent 1px);
  background-size: 
    60px 60px, 60px 60px,
    120px 120px, 120px 120px,
    180px 180px, 180px 180px;
  animation: sophisticatedGridMove 40s linear infinite;
  pointer-events: none;
  z-index: -1;
}

@keyframes sophisticatedGridMove {
  0% {
    transform: translate(0, 0) rotate(0deg);
    opacity: 0.5;
  }
  25% {
    transform: translate(30px, 30px) rotate(1deg);
    opacity: 0.7;
  }
  50% {
    transform: translate(60px, 60px) rotate(0deg);
    opacity: 0.6;
  }
  75% {
    transform: translate(90px, 90px) rotate(-1deg);
    opacity: 0.8;
  }
  100% {
    transform: translate(120px, 120px) rotate(0deg);
    opacity: 0.5;
  }
}

h1, h2, h3, h4 {
  font-family: 'Space Grotesk', 'Outfit', sans-serif;
  font-weight: 700;
}

.gradient-text {
  background-image: linear-gradient(to right, #60a5fa, #0ea5e9);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  background-size: 200% 200%;
  animation: gradient-shift 4s ease infinite;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: #0f172a;
}

::-webkit-scrollbar-thumb {
  background: var(--primary-color);
  border-radius: 100px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--secondary-color);
}

/* Glass effect styles */
.glass-effect {
  backdrop-filter: blur(16px) saturate(180%);
  background-color: var(--card-bg);
  border: 1px solid var(--border-light);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
}

.glass-card {
  backdrop-filter: blur(12px);
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.05), rgba(0, 0, 0, 0.05));
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 0.75rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
}

.text-glow {
  text-shadow: 0 0 5px var(--secondary-color);
}

.button-glow {
  box-shadow: 0 0 15px var(--primary-color), 0 0 30px var(--primary-color);
}

/* Modern Typography Utilities */
.text-balance {
  text-wrap: balance;
}

.text-pretty {
  text-wrap: pretty;
}

.text-truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.text-gradient-blue {
  background: linear-gradient(to right, #60a5fa, #0ea5e9);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.text-gradient-purple {
  background: linear-gradient(to right, #8b5cf6, #6366f1);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.text-gradient-gold {
  background: linear-gradient(to right, #f59e0b, #d97706);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

/* Grid layouts */
.auto-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  grid-gap: 1.5rem;
}

/* Modern spacing utilities */
.flow > * + * {
  margin-top: var(--flow-space, 1em);
}

/* Advanced background patterns */
.diagonal-lines {
  background-image: repeating-linear-gradient(
    45deg,
    rgba(59, 130, 246, 0.05),
    rgba(59, 130, 246, 0.05) 1px,
    transparent 1px,
    transparent 10px
  );
}

.grid-pattern {
  background-size: 20px 20px;
  background-image: 
    linear-gradient(to right, rgba(59, 130, 246, 0.05) 1px, transparent 1px),
    linear-gradient(to bottom, rgba(59, 130, 246, 0.05) 1px, transparent 1px);
}

.circuit-pattern {
  background-image: radial-gradient(rgba(59, 130, 246, 0.1) 2px, transparent 2px);
  background-size: 30px 30px;
  background-position: 0 0, 15px 15px;
}

/* Hover underline animation */
.hover-underline-animation {
  position: relative;
}

.hover-underline-animation::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: -4px;
  left: 0;
  background: linear-gradient(to right, #60a5fa, #0ea5e9);
  transform: scaleX(0);
  transform-origin: bottom right;
  transition: transform 0.3s ease;
}

.hover-underline-animation:hover::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

/* Custom animations */
@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

@keyframes pulse-glow {
  0%, 100% { box-shadow: 0 0 20px 5px rgba(59, 130, 246, 0.3); }
  50% { box-shadow: 0 0 30px 10px rgba(59, 130, 246, 0.5); }
}

@keyframes gradient-shift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

@keyframes ripple {
  0% { transform: scale(0.8); opacity: 1; }
  100% { transform: scale(1.4); opacity: 0; }
}

@keyframes shimmer {
  0% { background-position: -200% 0; }
  100% { background-position: 200% 0; }
}

@keyframes border-glow {
  0%, 100% { border-color: rgba(59, 130, 246, 0.7); }
  50% { border-color: rgba(14, 165, 233, 0.9); }
}

/* Additional animation keyframes for enhanced background effects */
@keyframes breathingGlow {
  0%, 100% {
    box-shadow: 0 0 20px rgba(59, 130, 246, 0.1);
    transform: scale(1);
  }
  50% {
    box-shadow: 0 0 40px rgba(59, 130, 246, 0.2);
    transform: scale(1.05);
  }
}

/* Additional sophisticated animation keyframes */
@keyframes etherealBreathing {
  0%, 100% {
    box-shadow: 
      0 0 20px rgba(59, 130, 246, 0.1),
      0 0 40px rgba(147, 197, 253, 0.05),
      inset 0 0 20px rgba(125, 211, 252, 0.03);
    transform: scale(1) rotate(0deg);
  }
  50% {
    box-shadow: 
      0 0 40px rgba(59, 130, 246, 0.2),
      0 0 80px rgba(147, 197, 253, 0.1),
      inset 0 0 40px rgba(125, 211, 252, 0.06);
    transform: scale(1.02) rotate(1deg);
  }
}

@keyframes dreamyFloat {
  0%, 100% {
    transform: translateY(0px) translateX(0px) rotate(0deg);
  }
  25% {
    transform: translateY(-15px) translateX(5px) rotate(90deg);
  }
  50% {
    transform: translateY(-25px) translateX(0px) rotate(180deg);
  }
  75% {
    transform: translateY(-15px) translateX(-5px) rotate(270deg);
  }
}

@keyframes sophisticatedShimmer {
  0% {
    background-position: -300% 0;
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    background-position: 300% 0;
    opacity: 0;
  }
}

@keyframes prismaticGlow {
  0%, 100% {
    filter: 
      drop-shadow(0 0 5px rgba(59, 130, 246, 0.3))
      drop-shadow(0 0 10px rgba(147, 197, 253, 0.2))
      drop-shadow(0 0 15px rgba(125, 211, 252, 0.1));
  }
  33% {
    filter: 
      drop-shadow(0 0 8px rgba(147, 197, 253, 0.4))
      drop-shadow(0 0 12px rgba(125, 211, 252, 0.3))
      drop-shadow(0 0 18px rgba(56, 189, 248, 0.2));
  }
  66% {
    filter: 
      drop-shadow(0 0 6px rgba(125, 211, 252, 0.35))
      drop-shadow(0 0 14px rgba(56, 189, 248, 0.25))
      drop-shadow(0 0 20px rgba(14, 165, 233, 0.15));
  }
}

/* Enhanced interactive hover effects with multiple layers */
.enhanced-animated-card {
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  position: relative;
  overflow: hidden;
  border-radius: 1rem;
}

.enhanced-animated-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    120deg,
    transparent 30%,
    rgba(59, 130, 246, 0.15) 50%,
    rgba(147, 197, 253, 0.1) 60%,
    transparent 70%
  );
  transition: left 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  z-index: 1;
}

.enhanced-animated-card::after {
  content: '';
  position: absolute;
  inset: 0;
  padding: 2px;
  background: linear-gradient(
    45deg,
    rgba(59, 130, 246, 0.2),
    rgba(147, 197, 253, 0.1),
    rgba(125, 211, 252, 0.15),
    rgba(56, 189, 248, 0.1)
  );
  border-radius: inherit;
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask-composite: exclude;
  opacity: 0;
  transition: opacity 0.4s ease;
}

.enhanced-animated-card:hover::before {
  left: 100%;
}

.enhanced-animated-card:hover::after {
  opacity: 1;
}

.enhanced-animated-card:hover {
  transform: translateY(-8px) rotateX(5deg);
  box-shadow: 
    0 25px 50px rgba(59, 130, 246, 0.2),
    0 15px 35px rgba(147, 197, 253, 0.15),
    0 5px 15px rgba(125, 211, 252, 0.1);
}

/* Aurora-like effect for special elements */
@keyframes auroraWave {
  0%, 100% {
    background-position: 0% 50%;
    opacity: 0.3;
  }
  25% {
    background-position: 100% 0%;
    opacity: 0.6;
  }
  50% {
    background-position: 0% 100%;
    opacity: 0.4;
  }
  75% {
    background-position: 100% 50%;
    opacity: 0.7;
  }
}

.aurora-effect {
  background: linear-gradient(
    45deg,
    rgba(59, 130, 246, 0.1),
    rgba(147, 197, 253, 0.15),
    rgba(125, 211, 252, 0.1),
    rgba(56, 189, 248, 0.12),
    rgba(14, 165, 233, 0.08)
  );
  background-size: 400% 400%;
  animation: auroraWave 8s ease-in-out infinite;
}

/* Performance and Mobile Optimizations for Animated Background */
@media (max-width: 768px) {
  body {
    background-attachment: scroll;
    animation-duration: 30s; /* Slower animations on mobile */
  }
  
  body::before,
  body::after {
    animation-duration: 35s, 20s;
  }
  
  .floating-particles::before,
  .floating-particles::after {
    animation-duration: 30s, 35s;
    background-size: 150px 150px, 200px 200px, 120px 120px; /* Smaller on mobile */
  }
  
  .aurora-background::before,
  .aurora-background::after {
    animation-duration: 20s, 25s;
    filter: blur(20px); /* Reduce blur for performance */
  }
  
  .nebula-glow::before,
  .nebula-glow::after {
    animation-duration: 20s, 25s;
    filter: blur(30px);
    width: 200px;
    height: 200px;
  }
}

/* Performance optimizations for animations */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* Mobile optimization for background animations */
@media (max-width: 768px) {
  body::before {
    animation-duration: 30s; /* Slower on mobile for performance */
  }
  
  body::after {
    animation-duration: 40s; /* Slower grid movement */
    background-size: 60px 60px; /* Smaller grid for better performance */
  }
  
  body {
    animation-duration: 25s; /* Slower gradient shift */
  }
}

/* High Performance Mode */
@media (prefers-reduced-motion: reduce) {
  body,
  body::before,
  body::after,
  .floating-particles::before,
  .floating-particles::after,
  .aurora-background::before,
  .aurora-background::after,
  .nebula-glow::before,
  .nebula-glow::after {
    animation: none !important;
  }
  
  body {
    background: linear-gradient(135deg, #030712 0%, #0f172a 50%, #1e293b 100%);
  }
}

/* GPU Acceleration for smooth animations */
.floating-particles,
.aurora-background,
.nebula-glow,
body::before,
body::after {
  will-change: transform, opacity;
  transform: translateZ(0);
  backface-visibility: hidden;
}

/* Dark mode specific background enhancements */
html.dark body {
  background: 
    linear-gradient(135deg, #020617 0%, #0c1425 50%, #1a1f36 100%),
    radial-gradient(circle at 25% 25%, rgba(59, 130, 246, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 75% 75%, rgba(14, 165, 233, 0.06) 0%, transparent 50%);
}

/* Disabled pattern overlay - too distracting */
.pattern-overlay {
  display: none; /* Completely disabled */
}

@keyframes pattern-drift {
  0% { transform: translate(0, 0); }
  100% { transform: translate(20px, 20px); }
}

/* Enhanced scroll-based background effects */
.background-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -10;
}

/* Intersection observer based animations */
.section-glow {
  position: absolute;
  width: 100%;
  height: 100vh;
  pointer-events: none;
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
  opacity: 0;
}

.section-glow.visible {
  opacity: 1;
}

.section-glow.hero {
  background: radial-gradient(ellipse at center top, rgba(59, 130, 246, 0.08) 0%, transparent 70%);
}

.section-glow.about {
  background: radial-gradient(ellipse at center, rgba(14, 165, 233, 0.06) 0%, transparent 70%);
}

.section-glow.projects {
  background: radial-gradient(ellipse at center, rgba(139, 92, 246, 0.05) 0%, transparent 70%);
}

.section-glow.contact {
  background: radial-gradient(ellipse at center bottom, rgba(16, 185, 129, 0.07) 0%, transparent 70%);
}

/* Enhanced animations for the background */
.mouse-interactive-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -2;
  background: 
    radial-gradient(600px circle at var(--mouse-x) var(--mouse-y), 
      rgba(59, 130, 246, 0.04), 
      transparent 40%);
  transition: background 0.2s ease;
}

/* Enhanced tilt and hover effects for cards */
.interactive-card {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  transform-style: preserve-3d;
}

.interactive-card:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 
    0 25px 50px rgba(0, 0, 0, 0.3),
    0 0 30px rgba(59, 130, 246, 0.2);
}

/* Dynamic background color shifts */
.section-bg-shift {
  transition: background-color 0.8s ease;
}

.section-bg-shift.hero {
  background: radial-gradient(ellipse at center, rgba(59, 130, 246, 0.02) 0%, transparent 70%);
}

.section-bg-shift.about {
  background: radial-gradient(ellipse at center, rgba(14, 165, 233, 0.02) 0%, transparent 70%);
}

.section-bg-shift.projects {
  background: radial-gradient(ellipse at center, rgba(139, 92, 246, 0.02) 0%, transparent 70%);
}

.section-bg-shift.contact {
  background: radial-gradient(ellipse at center, rgba(16, 185, 129, 0.02) 0%, transparent 70%);
}

/* Parallax scroll effects */
.parallax-element {
  will-change: transform;
  transition: transform 0.1s ease-out;
}

.parallax-slow {
  transform: translateY(calc(var(--scroll-y, 0) * -0.5px));
}

.parallax-medium {
  transform: translateY(calc(var(--scroll-y, 0) * -0.3px));
}

.parallax-fast {
  transform: translateY(calc(var(--scroll-y, 0) * -0.8px));
}

/* Breathing effect for key elements */
.breathing-glow {
  animation: breathing-glow 4s ease-in-out infinite;
}

/* Enhanced loading state backgrounds */
.loading-shimmer {
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0.1) 0%,
    rgba(255, 255, 255, 0.2) 50%,
    rgba(255, 255, 255, 0.1) 100%
  );
  background-size: 200% 100%;
  animation: loading-shimmer 2s infinite;
}

@keyframes loading-shimmer {
  0% { background-position: -200% 0; }
  100% { background-position: 200% 0; }
}
